.App {
  background-image: url('../src/assets/bg.svg');
  height: 100vh;
  width: 100vw;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align: center;
      align-items: center;
}

.form {
  width: 25vw;
}

.form > * {
  width: 100%;
  margin: 5px;
  padding: 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.contact-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  width: 75vw;
  height: 100vh;
  overflow-y: scroll;
}

.card {
  width: 210px;
  height: 200px;
  margin: 5px;
  padding: 5px;
  border: 1px solid gray;
  border-radius: 5px;
  background-color: white;
}

.editable {
  background-color: lavender;
}




